a {
  padding: 16px 24px;
  border-radius: 6px;
  text-align: center;
  white-space: nowrap;
  transform: scale(1);
  transition: 0.5s ease-in-out;
}

a:hover {
  transform: scale(1.1);
}

.innovation {
    background: var(--innovation-secondary-700);
}

.camsolBlackAndWhite {
    --bg: var(--camsol-black);
    --border: var(--camsol-white);
    --text: var(--camsol-white);
  }
  
  .technology {
    --bg: var(--camsol-white);
    --border: var(--technology-secondary-600);
    --text: var(--technology-secondary-600);
  }
  
  .platform {
    --bg: var(--platform-secondary-50);
    --text: var(--platform-secondary-700);
    --border: transparent;
  }

  @media only screen and (max-width: 900px) {
    a {
      width: 50%;
    }
  }