footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 93px 8vw;
  gap: 10px;
  letter-spacing: 0.02em;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px 0px;
  width: 100%;
}

.input_container {
  display: flex;
  gap: 25px;
  margin-top: 1rem;
}

.newsletter_span {
  display: flex;
  flex-direction: column;
}
.newsletter_span span {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: #222222;
}

.newsletter_span p {
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  color: #222222;
}

.input_container input {
  background: #f4f4f4;
  border: 1.5px solid #c5c5c5;
  border-radius: 8px;
  box-shadow: 0px 20px 25px -5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 16px;
  gap: 10px;
  width: 344px;
  height: 50px;
}

.input_container button {
  padding: 16px 24px;
  background-color: #036e67;
  border-radius: 6px;
}

.bottom_div {
  display: flex;
  width: 100%;
}

.logo_div {
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 30%;
}

.logo_div img {
  width: 150px;
}

.right_bottom {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

.main {
  font-weight: 700;
  font-size: 20px;
  line-height: 24.2px;
  font-style: normal;
  display: flex;
  align-items: center;
  color: #1c1c1c;
}

.right_bottom p {
  font-weight: 400;
  font-size: 14px;
  line-height: 16.94px;
  font-style: normal;
  display: flex;
  align-items: center;
  color: #1c1c1c;
}

.sub_container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

