ul {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
  align-items: center;
  width: 100%;
}

li {
  font-weight: 700;
  font-size: 20px;
  line-height: 24.2px;
  font-style: normal;
  display: flex;
  align-items: center;
  color: #1c1c1c;
  white-space: nowrap;
}

.copyright_div {
  width: 100%;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #1c1c1c;
}

.copyright_div a {
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width: 900px) {
  .input_container {
    flex-direction: column;
  }

  ul {
    flex-direction: column;
    align-items: flex-end;
  }

  .logo_div {
    width: 50%;
  }
}

@media only screen and (max-width: 600px) {
  .input_container input {
    width: 100%;
  }
  .bottom_div {
    flex-direction: column;
    gap: 10px;
  }

  .logo_div {
    width: 100%;
  }

  .container {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  ul {
    align-items: center;
    margin-top: 2rem;
  }

  ul li {
    margin-bottom: 1rem;
  }

  .copyright_div {
    flex-direction: column;
  }

  .copyright_div a {
    justify-content: center;
  }
}
