.back-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    align-items: center;
    justify-items: center;
    font-size: 100px;
    background: #02524D;
    height: 50px;
    /* width: 50px; */
    color: white;
    cursor: pointer;
    border-radius: 100px;
    padding-bottom: 8px;
    padding-top: 5px;
    padding-left: 6px;
    padding-right: 6px;
    border: none;
    box-shadow: 0 5px 10px #ccc;
}

.back-to-top:hover {
    background: #048A81;
}