.h-scroll-fade {
  position: relative;
}
.h-scroll-fade-after:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  background-image: linear-gradient(to right, rgba(255,255,255,0), #222222 100%);
  width: 25%;
  }

  .h-scroll-fade-after-light:after{
    content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  background-image: linear-gradient(to right, rgba(255,255,255,0), #ffffff 100%);
  width: 25%;
  }

  .h-scroll-fade-before:before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    bottom: 15px;
    pointer-events: none;
    background-image: linear-gradient(to left, rgba(255,255,255,0), #222222 100%);
    width: 25%;
    }

    .h-scroll-fade-before-light:before {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      bottom: 15px;
      pointer-events: none;
      background-image: linear-gradient(to left, rgba(255,255,255,0), #ffffff 100%);
      width: 25%;
      }

.no-scroll-bar::-webkit-scrollbar {
    @apply w-0;
}

.text-shade {
  text-shadow: 0 0 3px #ffffff, 1px 2px 4px #222222;
}



