.nav_links {
  position: relative;
align-self: self-start;
}

.links {
  white-space: nowrap;
  align-self:self-start;
}

.nav_links::before {
  content: "";
  position: absolute;
  width: 80%;
  background-color: #02524d;
  height: 2px;
  bottom: -1px;
  right: 0;
  transform: scaleX(0);
  transition: 0.2s ease-in-out;
}

.nav_links:hover::before {
  transform: scaleX(1);
}

.arrow {
  transform: rotate(0deg);
  transition: 0.2s ease-in-out;
}

.arrow.active {
  transform: rotate(-180deg);
}

.menu__toggler {
  display: none;
}

.menu {
  justify-content: flex-end;
  z-index: 100;
}

.btn_div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media only screen and (max-width: 1050px) {
  .menu__toggler {
    display: block;
  }

  .btn_div {
    justify-content: center;
  }

  .menu {
    position: absolute;
    top: 6.5rem;
    left: 0;
    background: #fff;
    width: 100% !important;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: 0 10px 10px 1px rgba(0, 0, 0, 0.1);
    padding: 20px 8vw;
    transform-origin: top;
    transform: scaleY(0);
    transition: 0.2s ease-in-out;
  }

  .menu.show {
    transform: scaleY(1);
  }

  .menu .links {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 884px) {
  .btn_div {
    justify-content: flex-start;
  }
}
